import { Canvas /*, useFrame*/, useFrame } from "@react-three/fiber";
import {
  OrbitControls,
  OrthographicCamera,
  CameraHelper
} from "@react-three/drei";

import useSpline from "@splinetool/r3f-spline";

import "./styles.css";

