const events = [
  { x: 0, y: -8, z: 0 },
  { x: 660, y: -8, z: 0 }
];
const tasks = [
  { x: 160, y: -8, z: 0 },
  { x: 425, y: -8, z: 100 },
  { x: 425, y: -8, z: -100 }
];
const gateways = [
  { x: 320, y: -8, z: 0 },
  { x: 530, y: -8, z: 0 }
];
const lines = [
  { x: 60, y: 0, z: 0 },
  { x: 270, y: 0, z: 0 },
  { x: 320, y: 0, z: 0, vertical: true },
  { x: 530, y: 0, z: 0, vertical: true },
  { x: 590, y: 0, z: 0 }
];
const curves = [
  { x: 365, y: 0, z: -50 },
  { x: 365, y: 0, z: 50, top: true },
  { x: 485, y: 0, z: -50, right: true },
  { x: 485, y: 0, z: 50, top: true, right: true }
];

export const angleToRadians = (angleInDeg) => (Math.PI / 180) * angleInDeg;

export default function App({ ...props }) {
  const { nodes, materials } = useSpline(
    "https://prod.spline.design/wfmSXp7i3VFZJXY4/scene.splinecode"
  );

  const Events = () =>
    events.map((event) => (
      <mesh
        name="Event"
        geometry={nodes.Event.geometry}
        material={materials.Gradient}
        castShadow
        receiveShadow
        position={[event.x, event.y, event.z]}
        rotation={[-Math.PI / 2, 0, 0]}
      />
    ));

  const Tasks = () =>
    tasks.map((task) => (
      <mesh
        name="Task"
        geometry={nodes.Task.geometry}
        material={materials.Gradient}
        castShadow
        receiveShadow
        position={[task.x, task.y, task.z]}
        rotation={[-Math.PI / 2, 0, 0]}
      />
    ));

  const Gateways = () =>
    gateways.map((gateway) => (
      <mesh
        name="Gateway"
        geometry={nodes.Gateway.geometry}
        material={materials.Clean}
        castShadow
        receiveShadow
        position={[gateway.x, gateway.y, gateway.z]}
        rotation={[-Math.PI / 2, 0, Math.PI / 4]}
      />
    ));

  const Lines = () =>
    lines.map((line) => (
      <mesh
        name="Line"
        geometry={nodes.Line.geometry}
        material={materials.Glass}
        castShadow
        receiveShadow
        position={[line.x, line.y, line.z]}
        rotation={[-Math.PI / 2, 0, Math.PI / (line.vertical ? 1 : 2)]}
      />
    ));

  const Curves = () =>
    curves.map((curve) => (
      <mesh
        name="Curve"
        geometry={nodes.Curve.geometry}
        material={materials.Glass}
        castShadow
        receiveShadow
        position={[curve.x, curve.y, curve.z]}
        rotation={[
          curve.top ? Math.PI / 2 : -Math.PI / 2,
          0,
          curve.right ? -Math.PI / 2 : 0
        ]}
      />
    ));

  return (
    <Canvas shadows flat linear>
      <directionalLight
        name="Light"
        castShadow
        intensity={0.52}
        shadow-mapSize-width={1024}
        shadow-mapSize-height={1024}
        shadow-camera-near={-10000}
        shadow-camera-far={100000}
        shadow-camera-left={-500}
        shadow-camera-right={500}
        shadow-camera-top={500}
        shadow-camera-bottom={-500}
        position={[200, 300, 300]}
      />

      <Events />
      <Tasks />
      <Gateways />
      <Lines />
      <Curves />

      <OrthographicCamera
        name="Personal Camera"
        makeDefault={true}
        zoom={0.85}
        far={100000}
        near={-10000}
        up={[0, 1, 0]}
        position={[0, 0, 1000]}
        rotation={[0, 0, 0]}
      />

      <OrbitControls
        enablePan={true}
        enableZoom={true}
        enableRotate={true}
        panSpeed="2"
      />

      <hemisphereLight
        name="Default Ambient Light"
        intensity={0.75}
        color="#eaeaea"
        position={[0, 1, 0]}
      />
    </Canvas>
  );
}
